.input {
  margin-bottom: 20px;
  position: relative;
  &__text {
    width: 100%;
    font-size: 20px;
    font-weight: 700;
    line-height: 120%;
    padding: 15px 20px;
    border-radius: 10px;
    background-color: #16182d;
    border: 1px solid #23243e;
    outline: none !important;
    color: #fff;
    position: relative;
    transition: all 150ms ease-in-out;

    &::placeholder {
      color: #9ca0d2;
    }
    &:focus {
      border-color: #18259b;
      background-color: #23243e;
      box-shadow: 0px 0px 13px rgba(0, 26, 255, 0.25);
    }
  }


}
